// Section Content List
// -----------------------

// REQUIRES...
//
// From _colours.scss
// $active-blue
// $active-blue-alt
// $light-grey
// $mid-grey
//
// From _vars.scss
// $spacing-2
// $spacing-3
//
// From _typography.scss
// @mixin body-s
// @mixin body-l
//
// From _layout.scss
// @mixin columns


.section-content-list {
  li {
    border-bottom: $light-grey solid 1px;
    counter-increment: li;
    padding: $spacing-3 #{$spacing-3 + 52px} $spacing-3 0;
    position: relative;
  }

  ol {
    list-style-type: none;

    li:before {
      @include heading-hero;
      background: none;
      color: $stone;
      content: counter(li) " ";
      display: block;
      font-size: 60px;
      left: -130px;
      line-height: 70px;
      position: absolute;
      text-align: right;
      width: 100px;


    }
  }

  .article {
    &:after {
      @include continue-arrow;
      position: absolute;
      right: 0;
      top: calc(50% - 26px);
    }

    &.article-read::after {
      background-image: url(/static/images/tick-purple.svg);
    }
  }

  .buttons {
    margin-bottom: $spacing-3;
    padding-top: $spacing-3;
    text-align: right;

    a {
      background-color: transparent;
      border-bottom: 3px solid $active-blue;
      padding: 10px 45px;
      padding-bottom: $spacing-3;
      padding-right: 0;
    }

    .previous {
      float: left;
      padding-left: 0;
      padding-right: $spacing-2;
    }
  }

  .tasks {
    display: block;
  }

  .section {
    @include body-s;
    color: $mid-grey;
    display: block;
  }

  .show-more {
    margin-left: -16.66667%;
    font-weight: 700;

    &:after {
      margin-right: $spacing-4;
    }
  }
}


// MOBILE
@media (max-width: 420px) {
  .section-content-list {
    width: 100%;

    ol {
      li:before {
        font-size: 40px;
        left: -120px;
        letter-spacing: -4px;
      }
    }

    .article {
      &:after {
        @include continue-arrow-mob;
      }
    }
  }
}
